:root {
  --pink-color: #ffbff7;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  width: 100vw;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.navbar {
	width: 100vw;
	height: 65px;
}

.navbar-content {
	position: fixed;
	display: flex;
	justify-content: center;
	width: 100%;
	min-height: 65px;
	background-color: var(--pink-color);
}

.navbar-delimiter {
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	width: 1000px;
	height: 100%;
	font-size: 20pt;
	color: white;
}

.navlink {
	flex: 0 1 20%;
    display: none;
	justify-content: center;
	align-items: center;
	min-height: 65px;
}

.navimg {
	flex: 1 1 30%;
    display: flex;
	justify-content: center;
    align-items: center;
	min-height: 65px;
}

.navimg img {
    max-height: 38px;
    max-width: 165px;
}

.navtop {
	display: flex;
	align-items: center;
	justify-content: center;
}

.navtop-side {
	flex: 0 0 10%;
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 10px;
}

.navmenu-open {
	display: flex;
}

@media screen and (min-width: 600px) {
	.navbar-delimiter {
		flex-direction: unset;
	}

	.navtop-side {
		display: none;
	}

	.navlink {
		display: flex;
	}
}
.background {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 350px;
	background-repeat: repeat-y;
	background-attachment: fixed;
	background-size: cover;
}

.background img {
	background-color: white;
	border-radius: 50%;
	max-height: 200px;
}
